import { AirplaneComponent } from './airplane/airplane';
import { IonicPageModule } from 'ionic-angular';
import { InquireComponent } from './inquire';
import { NgModule } from '@angular/core';
@NgModule({
    declarations: [InquireComponent, AirplaneComponent],
    imports: [
        IonicPageModule.forChild(InquireComponent)
    ],
    exports: [AirplaneComponent],
    entryComponents: [InquireComponent, AirplaneComponent]
})
export class InquireModule { }


